@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
body{margin:0;font-family:'Inter', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#00104b;background-color:#f4f7f7}input{-webkit-appearance:none;background:none;border:none;outline:none;font-family:inherit;font-size:16px;line-height:24px;padding:0.75rem 1.25rem;width:calc(100% - 2.5rem);color:#00104b}input::-webkit-input-placeholder{font-size:15px;color:#999fb7}input:-ms-input-placeholder{font-size:15px;color:#999fb7}input::-ms-input-placeholder{font-size:15px;color:#999fb7}input::placeholder{font-size:15px;color:#999fb7}::selection{background:#0077ff;color:#fff}.container{padding:2rem;width:calc(100% - 4rem);margin-left:auto;margin-right:auto}@media (min-width: 640px){.container{width:640px}}@media (min-width: 768px){.container{width:768px}}@media (min-width: 1024px){.container{width:1024px}}@media (min-width: 1280px){.container{width:1280px}}.form-group{padding:0.5rem 0}.form-group label{display:block;font-weight:700;padding-bottom:0.25rem}.form-group .form-controle{--border: #e6e7ee;--border-width: 1px;--border-focus: #0077ff;--border-focus-width: 2px;border-radius:8px;position:relative;display:flex;align-items:center;background:#fff;box-shadow:inset 0 0 0 var(--border-width) var(--border);transition:box-shadow 0.3s}.form-group .form-controle:focus-within{--border: var(--border-focus);--border-width: var(--border-focus-width)}

